#datepicker {
    display: flex;
    flex-direction: row;
}

.datepicker__header {
    display: flex;
    background-color: #f0f0f0;
    justify-content: center;
    padding: 5px;
    font-weight: bold;
    color: #3f51b5;
}

.datepicker__input {
    width: 100%;
    padding: 12px;
    border: 1px solid lightgray;
    color: #757575;
    border-radius: 999px;
    box-shadow: -1px 4px 20px -6px rgba(0,0,0,.65);
    margin-top: 4%;
    margin-bottom: 4%;
    background-color: white;
    margin-right: 5%;
    font-size: 18px;
}